/* 深圳市静江网络科技有限公司 */
*,*:before,*:after {-webkit-box-sizing:border-box; box-sizing:border-box;margin:0; padding:0;
}
html{-ms-text-size-adjust:100%;-webkit-text-size-adjust: 100%;width:100%;height:100%;
}
body{background:#eaedf1;position:relative; font:13px/1 "Microsoft YaHei", Helvetica, sans-serif;color:#333;width:100%;height:100%;
}
input{border:none;
}
input,select,option,textarea{outline: none;font-size: 13px;font-family: "Microsoft YaHei";
}
input[type="button"],button,select{cursor: pointer;border: none;outline: none;
}
textarea{outline: none;
}
ul,li,h1,h2,h3,h4,h5,h6{list-style-type:none;
}
img{border:none;
}
a{text-decoration:none;color: #666;
}
a:hover{text-decoration:none;
}
.clf{zoom:1
}
.clf:before,.clf:after,.w:before,.w:after{content:"";display:block;height:0;clear:both;visibility:hidden;
}
.fl{float: left;
}
.fr{float: right!important;
}
/* 初始化样式结束 */
#admin{height: 100%;
}
/* 弹窗 */
.delete_prompt{width: 100%;height: 100%;background: rgba(0,0,0,0.2);position:fixed;top: 0;left: 0;/*display: none;*/
}
